		--height : 2em;
		--width  : 2em;
		--padding: 0.4em;
	}

	.BaseCheckbox {	// layout
		display: inline-block;
		flex-shrink: 0;

		&__inner {
			display: flex;
			padding: var(--padding);
		}
		&__fakeCheckbox {
			position: relative;
			pointer-events: none;
			flex-grow: 1;

			svg {
				position: absolute;
				top: 1; left: 1; right: 1; bottom: 1;
				opacity: 0;
			}

			&--isSelected svg { opacity: 1; }
		}
	}
	.BaseCheckbox { // styling
		&__fakeCheckbox {
			background-color: fade( red, 15 );
			background-color: white;
			border-radius: var(--borderRadius);
			cursor: pointer;
		}
	}

