.BaseRadio { // css vars
	--height : 2em;
	--width  : 2em;
	--padding: 0.4em;
}

.BaseRadio {	// layout
	display: inline-block;
	flex-shrink: 0;

	&__inner {
		display: flex;
		padding: var(--padding);
	}
	&__fakeRadio {
		position: relative;
		pointer-events: none;
		flex-grow: 1;
	}
}
.BaseRadio { // styling
	&__inputRadio + &__fakeRadio circle {
		transition: var(--transition);

		&:first-child {
			stroke: currentColor;
			stroke-width: 1.25px;
		}
		&:last-child {
			r: 5.25px;
			fill: white;
		}
	}
	&__inputRadio:checked + &__fakeRadio circle {
		&:first-child {}
		&:last-child {
			r: 2px;
		}
	}
}

.BaseRadio { // hover
}
.BaseRadio { // focus
}
.BaseRadio { // disabled
